@import '../../styles/mixins';
@import '../../styles/variables';

a.wrapper {
  position: relative;
  display: flex;
  align-items: center;
  max-width: max-content;
  margin-bottom: 6px;
  color: $red-light;
  line-height: 19px;

  &:hover {
    .label {
      &::before {
        width: 100%;
      }
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}  
  
.label {
  position: relative;
  padding-top: 6px;
  padding-bottom: 6px;

  &::before {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 1px;
    background-color: $red-light;
    transform: translateX(-50%);
    transition: all .5s ease;
    content: '';
  }
}

.icon {
  margin-right: 6px;
  font-size: 24px;
}